<template>
    <div class="container">
        <Aside 
          :status="asideStatus" 
          :navData="navInfo"
          @getData="getArticalData" />
       <div class="bd-container">
          <Header 
            :navList="navList"
            @setAsideStatus="setAsideStatus"
            @getNavInfo="getNavInfo"
          />
        <div class="bd-content scroll-container">
          <div class="content">
            <div v-html="articles && articles.content"></div>
          </div>
        </div>
       </div>
    </div>
</template>

<script>
import Header from '../components/header.vue'
import Aside from '../components/aside.vue'
import Fetch from '../utils/fetch'

export default {
  components:{
    Header,
    Aside,
  },
  data(){
    return {
      isContent: false,
      isList: true,
      content: '',
      mdTile: '',
      articles: [],
      asideStatus: '',
      navInfo: {},
      navList: []
    }
  },
  created() {
    this.getNavList()
  },
  mounted() {
    this.getArticalData('article')
  },
  computed: {
      
  },
  methods: {
    getArticalData(fileName) {
      Fetch(`static/parsefile/${fileName}.json`).then(res => {
        this.articles = res.data;
      })
    },
    getNavList() {
      Fetch(`static/data-config/nav.json`).then(res => {
        console.log(11111, res)
        this.navList = res.data;
      })
    },
    setAsideStatus(val) {
      console.log(111111, val, this.asideStatus)
      if (val) {
        if (!this.asideStatus) return
        this.asideStatus = val;
        return
      }
      if (!this.asideStatus || this.asideStatus === 'open') {
        this.asideStatus='hide'
      } else {
        this.asideStatus = 'open'
      }
    },
    getNavInfo(nav) {
      if (nav) this.navInfo = nav
    },
    clipPage(content){
        this.content = content;
        console.log(this.content)
        this.isContent = false;
        this.isList = false;
    },
    goIndex(){
        this.isContent = false;
        this.isList = true;
    }
  }
}
</script>

<style lang='scss' scoped>
.container{
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
}
.bd-container {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.bd-content{
  flex: 1;
  display: flex;
  justify-content: center;
  background: #fff;
}
.content {
  width: 680px;
}
</style>
