<template>
  <div class="aside" :class="getAsideClass">
    <div class="aside-logo">
      <img class="logo-img" src="../assets/images/logo.png" alt="头像" srcset="">
    </div>
    <div class='aside-brief'>
      <img class="head-img" src="../assets/images/head.jpg" alt="头像" srcset="">
      <div class="head-info">
        <span><strong>哈皮艾瑞day</strong> </span>
        <span>前端之路...</span>
        <span>岂是一句‘卧槽’能概括</span>
      </div>
    </div>
    <div class="aside-list" v-if="navInfo.children && navInfo.children.length">
      <div 
        v-for="(item, index) in navInfo.children" 
        :key="index"
        class="aside-list-item">
        <div 
          class="aside-item" 
          @click="handleClickListItem(item)"
          :class="getIconActive(item)">
          <span>{{ item.value }}</span>
        </div>
        <div 
          v-if="item.children"
          class="aside-sub-items" 
          :style="item.style">
          <div 
            v-for="(child, cindex) in item.children"
            :key="cindex"
            :class="getSubStyle(child)"
            @click="hadleClickSubItem(child, item.children)"
            class="aside-sub-item"
          >
            {{child.value}}
          </div>
        </div>
      </div>
    </div>
    <Empty v-else/>
  </div>
</template>

<script>
import { isRealArr } from "../utils";
import Empty from '../components/empty.vue'

export default {
  components: {
    Empty
  },
  props: {
    status: {
      type: String,
      default: ''
    },
    navData: {
      type: Array,
      default: () => {}
    }
  },
  data() {
    return {
    }
  },
  computed: {
    getAsideClass() {
      if (this.status === 'open' ) return 'aside-open'
      if (this.status === 'hide') return 'aside-hide'
      return ''
    },
    navInfo() {
      return this.navData
    },
  },
  methods: {
    getIconActive(child) {
      const {children, isActive} = child || {}
      if (!isRealArr(children)) return ''
      if (isActive) return 'aside-item-icon aside-item-up'
      return 'aside-item-icon'
    },
    handleClickListItem(item) {
      item.isActive = !item.isActive
      const {children, isActive} = item
      item.style = {
        height: isActive&&isRealArr(children) ? `${children.length*60}px` : 0
      }
    },
    hadleClickSubItem(child) {
      this.navInfo.children.forEach(item => {
        item.children.forEach((val, index) => {
          item.children[index].isActive = false
        })
      })
      child.isActive = !child.isActive
      this.$emit('getData', child.key)
    },
    getSubStyle(child) {
      const {isActive} = child;
      if (isActive) return 'aside-sub-item-active'
      return ''
    }
  }
}
</script>

<style lang='scss' scoped>
.aside {
  width: 380px;
  height: 100%;
  box-sizing: border-box;
  background: #fefdfc;
  border-right: 1px solid #e8e8e8;
  word-wrap: none;
  white-space: nowrap;
  display: flex;
  flex-direction: column;
  &-logo {
    height: 66px;
    .logo-img {
      width: 380px;
    }
  }
  &-brief {
    box-sizing: border-box;
    display: flex;
    height: 140px;
    padding: 20px 20px 20px 40px;
    border-bottom: 1px solid #e9e9e9;
    .head-img {
      height: 99px;
      width: 99px;
      border-radius: 2px;
      box-shadow: 3px 3px 2px #888;
    }
    .head-info {
      flex: 1;
      box-sizing: border-box;
      margin-left: 10px;
      display: flex;
      flex-direction: column;
      span {
        margin: 5px 0;
      }
    }
  }
  &-list {
    flex: 1;
    overflow-y: auto;
  }
  &-list-item {
    border-bottom: 1px solid #e9e9e9;
  }
  .aside-item {
    height: 71px;
    box-sizing: border-box;
    padding: 20px 20px 20px 40px;
    line-height: 30px;
    position: relative;
    cursor: pointer;
    color: #404040;
    font-weight: 600;
  }
  .aside-item-icon {
    &::after {
      position: absolute;
      content: '';
      width: 20px;
      height: 20px;
      top: 25px;
      right: 20px;
      background: url('../assets/icon/chevron-down.svg') no-repeat;
    }
  }
  .aside-item-up {
    &::after {
      transform: rotate(-180deg);
    }
  }
  .aside-sub-items {
    overflow: hidden;
    transition: height .3s ease;
  }
  .aside-sub-item {
    height: 60px;
    box-sizing: border-box;
    line-height: 60px;
    padding-left: 60px;
    color: #4c4c4c;
    cursor: pointer;
    &:hover {
      color: #409EFF;
      background: rgba(64, 158, 255, 0.1);
    }
  }
  .aside-sub-item-active {
    color: #409EFF;
    background: rgba(64, 158, 255, 0.1);
  }
}
.aside-hide {
  animation: asideHide .6s ease;
  animation-fill-mode: forwards;
}
.aside-open {
  animation: asideOpen .6s ease;
  animation-fill-mode: forwards;
}
@keyframes asideHide {
  from {
    width: 380px;
    border-right: 1px solid #e8e8e8;
  }
  to {
    width: 0;
    border: none;
  }
}
@keyframes asideOpen {
  from {
    width: 0;
    border: none;
  }
  to {
    width: 380px;
    border-right: 1px solid #e8e8e8;
  }
}
</style>