<template>
  <div class="header">
    <div class="hd-logo">
      <span 
        class="switch"
        :class="isClose ? 'switch-open' : 'switch-close'"
        @click="setAsideStatus('')"></span>
      <span class="hd-name" :style="{color: isClose ? '#fa8919':'#409EFF'}">caiwenHub</span>
    </div>
    <div class="hd-nav">
      <span
        v-for="nav in navList"
        :key="nav.key"
        :class="getNavItemClass(nav.isActive)"
        @click="handleNavItemClick(nav)" >
        {{ nav.value }}
      </span>
    </div>  
  </div>
</template>

<script>
export default {
  props: {
    navList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      isClose: false
    }
  },
  methods: {
    setAsideStatus(val) {
      if (!val) {
        this.isClose = !this.isClose
      } else {
        this.isClose = !(val === 'open')
      }
      
      this.$emit('setAsideStatus', val)
    },
    getNavItemClass(isActive) {
      return isActive 
        ? 'hd-nav-item hd-nav-item-active'
        : 'hd-nav-item'
    },
    handleNavItemClick(nav) {
      this.navList.forEach(item => {
        if (nav.key === item.key) {
          item.isActive = true;
        } else {
          item.isActive = false;
        }
      })
      this.setAsideStatus('open')
      this.$emit('getNavInfo', nav)
    }
  }
}
</script>

<style lang='scss'>
.header {
  height: 66px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 0 20px;
  background: #fff;
  color: #333;
  border-bottom: 1px solid #e9e9e9;
  box-shadow: 2px 0px 4px #409EFF;
  margin-bottom: 1px;
}
.hd-logo {
  position: relative;
  .switch {
    position: absolute;
    top: 2px;
    left: 0;
    width: 24px;
    height: 24px;
    cursor: pointer;
  }
  .switch-close {
    background: url('../assets/icon/close.svg') no-repeat;
    animation: switchClose 1s infinite;
  }
  .switch-open {
    background: url('../assets/icon/open.svg') no-repeat;
    animation: switchOpen 1s infinite;
  }
  .hd-name {
    padding-left: 40px;
    font-size: 24px;
    font-weight: bold;
    color: #409EFF;
    user-select: none;
  }
  @keyframes switchOpen {
    0% { left: 0; }
    25% { left: 2px; }
    50% { left: 4px; }
    75% { left: 2px; }
    100% { left: 0; }
  }
  @keyframes switchClose {
    0% { left: 0; }
    25% { left: -2px; }
    50% { left: -4px; }
    75% { left: -2px; }
    100% { left: 0; }
  }
}
.hd-nav {
  &-item {
    display: inline-block;
    width: 80px;
    height: 66px;
    line-height: 66px;
    text-align: center;
    cursor: pointer;
    font-weight: 400;
    box-sizing: border-box;
    user-select: none;
    &:hover {
      color: #409EFF;
      border-bottom: 2px solid #409EFF;
    }
  }
  &-item-active {
    background: rgba(64, 158, 255, 0.1);
    color: #409EFF;
    font-weight: bold;
  }
}
</style>